@import "index";
@import "bootstrap/scss/forms";

// All form controls, inputs, buttons, etc.
.form-control {
  font-family: inherit;

  &.error {
    border-color: $brand-danger;
  }
}

// Form labels.
.form-control-label {
  font-weight: $label-font-weight;
  // font-size: $label-font-size;
  color: $label-color;
  cursor: pointer;
}

// Inline forms get slightly modified <label>s.
.form-inline {
  .form-group {
    display: block;
    margin-top: $spacer-y;
  }
  .form-control {
    margin-top: 0;
  }
  .form-control-label {
    text-transform: uppercase;
    font-weight: $font-weight-normal;
  }
}

.text-format-wrapper .form-group {
  margin-bottom: 0;
}

.form-description {
  color: color(grey);
  display: block;
}

.form-group,
.checkbox {
  // This is needed to fix weird line-heights with <small>.
  > .form-description {
    display: inline-block;
    margin-top: .5rem;
  }
}

// Bootstrap uses `has-danger`, but `has-error` is much better.
.has-error {
  @include form-control-validation($brand-danger);

  .form-control-label {
    color: $brand-danger;
  }
}

/**
 * @file
 * Fieldgroup border reset.
 *
 * @todo What is this? Replace with Bootstrap styles.
 */
.fieldgroup {
  border-width: 0;
  padding: 0;
}
